<template>
    <div class="searchWrap">
        <header>
            <i class="el-icon-arrow-left" @click="goBack"></i>
            <div class="title_head ellipsis"><span>搜索</span></div>
        </header>
        <div class="search_form">
            <el-input
                    placeholder="请输入商家或美食名称"
                    v-model="inputV"
                    clearable>
            </el-input>
            <button @click="submitSerch">提交</button>
            <i class="close_icon"  @click="closeInput"></i>
        </div>
        <section>
            <h4 v-if="isShow">商家</h4>
            <ul class="list_container" v-if="isShow">
                <li v-for="(item, index) in search_shop">
                    <section class="item_left">
                        <img :src="'https://elm.cangdu.org/img/'+item.image_path" alt="">
                    </section>
                    <section class="item_right">
                        <div>
                            <p class="p1">
                                <span>{{item.name}}</span>
                                <span class="pay">支付</span>
                            </p>
                            <p>月售{{item.recent_order_num}}单</p>
                            <p>{{item.float_minimum_order_amount}}元起送 / 距离{{item.distance}}</p>
                        </div>
                        <ul></ul>
                    </section>
                </li>
            </ul>
        </section>
        <section class="search_history" v-if="isShow2">
            <h4>搜索历史</h4>
            <ul class="history_list">
                <li v-for="(his, m) in history_list" :key="m">
                    <span class="ellipsis">{{his}}</span>
                    <i class="el-icon-close" @click="clear_li(m)"></i>
                </li>
            </ul>
            <div @click="clearAll">清空搜索历史</div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Search",
        data(){
            return {
                inputV:"",
                address:null,
                isShow:false,
                isShow2:false,
                search_shop:[],
                history_list:[]
            }
        },
        created(){
            if(localStorage.getItem("search_history")){
                this.isShow2 = true;
                this.history_list = localStorage.getItem("search_history").split(",");
            }
            this.$parent.isChoose.id = 2;
            this.address = localStorage.getItem("geohash");
        },
        methods:{
            goBack(){
                this.$router.go(-1);
            },
            submitSerch(){
                if(!this.inputV.trim().length){
                    return;
                }
                this.isShow = true;
                this.isShow2 = false;
                this.axios.get("/v4/restaurants",{
                    params:{
                        geohash:this.address,
                        keyword:this.inputV
                    }
                }).then(res=>{
                    this.search_shop = res;
                    console.log(res);
                }).catch(err=>{});
                if(!localStorage.getItem("search_history")){
                    localStorage.setItem("search_history",this.inputV);
                    this.history_list.push(this.inputV);
                }else {
                    let history = localStorage.getItem("search_history").split(",");
                    let arr = history.filter(v=>{
                        return v == this.inputV;
                    });
                    if(!arr.length){
                        history.push(this.inputV);
                        localStorage.setItem("search_history",history);
                        this.history_list.push(this.inputV);
                    }
                }
            },
            clearAll(){
                this.isShow2 = false;
                localStorage.removeItem("search_history");
                this.history_list.splice(0,this.history_list.length);
            },
            closeInput(){
                this.inputV = "";
                this.isShow = false;
                if(this.history_list.length){
                    this.isShow2 = true;
                }
            },
            clear_li(n) {
                this.history_list.splice(n,1);
                localStorage.setItem("search_history",this.history_list);
                if(!this.history_list.length){
                    this.isShow2 = false;
                }
            }
        }
    }
</script>

<style scoped>
    .search_history>h4 {
        font-size: .6rem;
        line-height: 2rem;
        text-indent: .5rem;
        font-weight: 700;
        color: #666;
    }
    .search_history>div {
        background-color: #fff;
        color: #3190e8;
        font: .7rem/2rem Microsoft YaHei;
        font-weight: 700;
        text-align: center;
    }
    .history_list>li {
        background-color: #fff;
        border-bottom: .025rem solid #e4e4e4;
        font: .7rem/2rem Microsoft YaHei;
        padding: 0 .3rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .history_list>li>span {
        display: inline-block;
        width: 80%;
    }
    .history_list>li>i {
        font-size: .8rem;
        font-weight: bolder;
        color: rgb(153, 153, 153);
    }
    .searchWrap {
        margin-bottom: 2rem;
        padding-top: 1.95rem;
    }
    .searchWrap>header>i {
        margin-left: .4rem;
        line-height: 2rem;
        color: #fff;
        font-size: 1rem;
    }
    .title_head {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        text-align: center;
    }
    .title_head>span {
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    .search_form {
        background-color: #fff;
        padding: .5rem;
        display: flex;
        position: relative;
    }
    .close_icon {
        display: block;
        width: .8rem;
        height: .8rem;
        position: absolute;
        top: .9rem;
        left: 10.8rem;
    }
    .search_form>button {
        flex: 1;
        border: .025rem solid #3190e8;
        margin-left: .2rem;
        font-size: .65rem;
        color: #fff;
        border-radius: .125rem;
        background-color: #3190e8;
        font-weight: 700;
        padding: 0 .25rem;
        height: 1.5rem;
    }
    .searchWrap>section>h4 {
        font-size: .6rem;
        line-height: 2rem;
        text-indent: .5rem;
        font-weight: 700;
        color: #666;
    }
    .list_container {
        background-color: #fff;
    }
    .list_container>li {
        display: flex;
        padding: .5rem;
        border-bottom: .025rem solid #e4e4e4;
    }
    .item_left {
        margin-right: .25rem;
    }
    .item_left>img {
        width: 1.7rem;
        height: 1.7rem;
    }
    .item_right {
        font-size: .55rem;
        flex: 1;
    }
    .item_right>div {
        padding-bottom: .25rem;
        border-bottom: .025rem solid #e4e4e4;
    }
    .item_right>ul {
        margin-top: .25rem;
    }
    .item_right>div>p {
        line-height: .9rem;
    }
    .p1 {
        display: flex;
        align-items: center;
    }
    .pay {
        color: rgb(255, 96, 0);
        font-size: 9px;
        font-weight: bold;
        margin-left: .2rem;
        border: 1px solid rgb(255, 96, 0);
        display: inline-block;
        height: .5rem;
        line-height: .5rem;
        transform: skewX(-20deg);
        text-decoration: underline;
    }
</style>
<style>
    .el-input {
        flex: 4;
    }
    .el-input__inner {
        border: .025rem solid #e4e4e4;
        font-size: .65rem;
        color: #333;
        border-radius: .125rem;
        background-color: #f2f2f2;
        font-weight: 700;
        padding: 0 .25rem;
        height: 1.5rem;
    }
</style>